import './index.css'; // eslint-disable-next-line

console.log('hello');


// 注册 serviceworker
// 处理兼容性问题
if ('serviceworker' in navigator) {
    window.addEventListener('load', () => {
        navigator.serviceWorker.register('/service-worker.js')
            .then(() => {
                console.log('sw注册成功了~');
            })
            .catch(() => {
                console.log('sw注册失败了~');
            });
    });
}
